news = news.filter((x) => x.type !== "paper");

const getIconByType = (e: string) => {
    switch (e) {
        case "paper":
            return '<i class="fas fa-print" style="color:#247BA0"></i>';
        case "award":
            return '<i class="fas fa-trophy" style="color:#94771e;"></i>';
        case "travel":
            return '<i class="fas fa-briefcase" style="color:#F25F5C;"></i>';
        case "media":
            return '<i class="fas fa-newspaper" style="color:#b38eb3"></i>';
        case "business":
            return '<i class="fas fa-registered" style="color:#484D6D"></i>';
    }
};

const tags = [
    {
        type: "all",
        name: "All",
    },
    {
        type: "award",
        name: "Award",
    },
    // {
    //     type: "paper",
    //     name: "Academic",
    // },
    {
        type: "business",
        name: "R&D",
    },
    {
        type: "media",
        name: "Media",
    },
    {
        type: "travel",
        name: "Outreach",
    },
];

export default defineComponent({
    name: "App",
    components: {},
    setup() {
        const state = reactive({
            currentTab: "all",
            activeKey: "",
        });

        for (let n of news) {
            n.icon = getIconByType(n.type);
        }

        var filteredNews = computed(() => {
            if (state.currentTab === "all") return news;
            else {
                var type = tags.find((x) => x.type === state.currentTab)?.type;

                if (type !== undefined) {
                    return news.filter((x) => x.type === type);
                }
            }
        });

        return {
            ...toRefs(state),
            news,
            tags,
            filteredNews,
        };
    },
    computed: {},
});
