export default defineComponent({
    name: "App",
    components: { SinglePaper, CommentOutlined, PaperLink },
    setup() {
        for (let p of publications) {
            p.img = require(`@/assets/images/${p.imgurl}`);
        }

        const tags = [
            "All",
            ..._.chain(publications)
                .groupBy((x) => x.tag)
                .map((v, key) => key)
                .value(),
        ];
        console.log(tags);

        const state = reactive({
            yearFilter: [],
            currentTab: "All",
        });

        var filteredPub = computed(() => {
            console.log(state.currentTab, state.currentTab == "All");

            if (state.currentTab == "All") return publications;
            else return publications.filter((p) => p.tag === state.currentTab);
        });

        return {
            publications,
            ...toRefs(state),
            tags,
            filteredPub,
        };
    },
    computed: {},
});
